const CONTRACT_NAME = process.env.CONTRACT_NAME || 'nearspring1.springus.testnet'

const { networkId } = getConfig(process.env.NODE_ENV || 'development')
const METHOD_TYPE = "hello"

$("#PushHello").click(function(event) {
  event.preventDefault()
  var nameIn = $("name").val()
  parametersIn = btoa(JSON.stringify({ name: nameIn }))

  var settings = {
    url: "https://rpc.testnet.near.org'",
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: JSON.stringify({
      jsonrpc: "2.0",
      id: "nothing",
      method: "query",
      params: {
        request_type: "call_function",
        finality: "final", 
        account_id: CONTRACT_NAME,
        method_name: METHOD_TYPE,
        args_base64: parametersIn,
      },
    }),
  }

  $.ajax({
    ...settings,
    success: function(result) {
      console.log(result)
      resultJson = JSON.stringify(result)
      resultText = JSON.stringify(JSON.parse(resultJson).result)
      resultFinal = String.fromCharCode.apply(String, JSON.parse(resultText).result)
      
      $("#result".addClass)('active');
      $("#result").html(resultFinal);
    },
    error: function(event) {
      alert(event)
    },
  })
})